.img-fluid {
    max-width: 100%;
    height: auto;
}

.img-thumbnail {
    padding: 0.25rem;
    background-color: #fff;
    border: 1px solid #ccd0d4;
    border-radius: 0.25rem;
    max-width: 100%;
    height: auto;
}

.figure {
    display: inline-block;
}

.figure-img {
    margin-bottom: 0.5rem;
    line-height: 1;
}

.figure-caption {
    font-size: 0.875em;
    line-height: calc(4px + 2ex);
    color: #6c757d;
}

figure:last-child {
    margin-bottom: 0;
}

.figure-img:last-child {
    margin-bottom: 0;
}

.media-wrapper {
    display: block;
    margin-inline-start: 0;
    margin-inline-end: 0;
}
.media-wrapper:not(:last-child) {
    margin-bottom: 1em;
}
.media-wrapper:last-child {
    margin-bottom: 0;
}
.media-wrapper.gallery-wrapper {
    width: 100%;
    display: block;
}
.media-wrapper.is-bg {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    min-width: 100%;
    min-height: 100%;
    margin-bottom: 0 !important;
}
.media-wrapper.is-bg .media-link,
.media-wrapper.is-bg .media-content {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    min-width: 100%;
    min-height: 100%;
}
.media-wrapper.is-bg .wistia_embed.no-video-foam {
    position: absolute;
    height: 100% !important;
}
.media-wrapper.is-bg .wistia_embed.no-video-foam::before {
    display: none;
}
.media-wrapper.has-caption {
    display: grid;
    align-items: center;
    justify-content: center;
    grid-template-columns: 1fr;
    place-items: center;
    place-content: space-evenly;
    grid-auto-columns: auto;
}
.media-wrapper.has-caption.caption-top .media-content {
    grid-row-start: 1;
}
@media (min-width: 768px) {
    .media-wrapper.has-caption.caption-left {
        grid-template-columns: unset !important;
        grid-auto-flow: column;
    }
}
@media (min-width: 768px) {
    .media-wrapper.has-caption.caption-left .media-content {
        grid-column-start: 1;
    }
}
@media (min-width: 768px) {
    .media-wrapper.has-caption.caption-left .media-caption {
        text-align: right;
    }
}
@media (min-width: 768px) {
    .media-wrapper.has-caption.caption-right {
        grid-template-columns: unset !important;
        grid-auto-flow: column;
    }
}
@media (min-width: 768px) {
    .media-wrapper.has-caption.caption-right .media-caption {
        text-align: left;
    }
}
.media-wrapper.has-caption .media-caption {
    text-align: center;
}
.media-wrapper.media-video .ls-blur-up-img {
    display: none;
}

.media-video {
    width: 100%;
    height: auto;
}

.media-caption {
    font-size: 0.875em;
    line-height: calc(4px + 2ex);
}

.media-content {
    display: block;
    overflow: hidden;
    position: relative;
    width: 100%;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
}
.media-content.ratio .media-src {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    min-width: 100%;
    min-height: 100%;
    width: 100%;
    height: 100%;
    display: block;
    object-fit: cover;
    font-family: "object-fit: cover; parent-fit: cover";
}

ul.gallery-slider,
ul.tns-slider {
    margin-bottom: 0;
}

svg,
img {
    max-width: 100%;
    height: auto;
